.calculator {
    width: 300px;
    margin: 20px auto;
    background-color: #4f44e0;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 20px;
  }

  .display {
    border: 1px solid #fdf2f2;
    background-color: #f6ebeb;

    border-radius: 5px;
    padding: 10px;
    text-align: right;
    margin-bottom: 10px;
  }
  
  input {
    width: calc(100% - 20px);
    border: none;
    outline: none;
    font-size: 1.2em;
  }
  
  .result {
    font-size: 1.5em;
    margin-top: 5px;
    color: #333;
  }
  
  .buttons {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 10px;
  }
  
  button {
    width: 100%;
    padding: 15px;
    font-size: 1.2em;
    background-color: #1c109c;
    color: #e9e9f8;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  button:hover {
    background-color: #45a049;
  }
  
  button:active {
    background-color: #3e8e41;
  }
  
  /* Clear button styles */
  button[data-value="C"] {
    background-color: #ff5c5c;
  }
  
  button[data-value="C"]:hover {
    background-color: #e64848;
  }
  
  button[data-value="C"]:active {
    background-color: #d14444;
  }
  
  /* Operator button styles */
  button[data-type="operator"] {
    background-color: #f0ad4e;
  }
  
  button[data-type="operator"]:hover {
    background-color: #eea236;
  }
  
  button[data-type="operator"]:active {
    background-color: #d19b3f;
  }
  